import React, { useContext } from 'react'
import { TodoForm } from '../components/forms/TodoForm'
import { ModalForm } from '../components/ModalForm'
import { APILocation, TokenContext } from '../App'
import { DeleteButton } from './forms/DeleteButton'

export function Todo({ parentId, title, priority, status, todo, fetchTodos}) {
    const token = useContext(TokenContext)
    const modalId = "updateTodo"

    return (
        <div className="accordion-item">
            <h2 className="accordion-header" id={"flush-heading" + todo.id}>
                <button className="accordion-button" type="button" data-bs-toggle="collapse"
                    data-bs-target={"#flush-collapse" + todo.id} aria-expanded="true" aria-controls={"flush-collapse" + todo.id}>
                    <small className={"bx bxs-circle me-1 priority-" + status}></small>
                    {title}
                    <div className="badge bg-primary ms-3">{priority}</div>
                </button>
            </h2>
            <div id={"flush-collapse" + todo.id} className="accordion-collapse collapse show" aria-labelledby={"flush-heading" + todo.id}
                data-bs-parent={`#${parentId}`}>
                <div className="accordion-body">
                    <div className="d-grid gap-2 d-md-block">
                        <button className="btn btn-info btn-sm me-2" type="button"
                            data-bs-toggle="modal" data-bs-target={`#${modalId}`}>Modifier</button>
                        <ModalForm id={modalId} title={"Update a Todo"}><TodoForm action={"update"} method={"PUT"}
                            apiEndPoint={APILocation + "/todos/" + todo.id} token={token} fetchTodos={fetchTodos} /></ModalForm>
                        <DeleteButton method={"DELETE"} todoId={todo.id} fetchTodos={fetchTodos} />
                    </div>
                </div>
            </div>
        </div>
    )
}
