import React, { useRef, useState, useEffect } from 'react'
import { ValidationInput } from "../components/ValidationInput"
import { APILocation, loginRoute } from '../App'
import { Link } from 'react-router-dom'

export function Register() {
    const [isSubmitting, setIsSubmitting] = useState(false)
    const [message, setMessage] = useState("")
    const [alertColor, setAlertColor] = useState("bg-danger")
    const alertRef = useRef(null)

    const [validationFeedback, setValidationFeedback] = useState({
        username: { status: '', message: '' },
        password: { status: '', message: '' },
        passwordConfirm: { status: '', message: '' }
    })

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/

    useEffect(() => {
        if (message) {
            alertRef.current.classList.remove("d-none")
            alertRef.current.classList.remove(alertColor)
            alertRef.current.classList.add(alertColor)
        } else {
            alertRef.current.classList.add("d-none")
        }
    }, [message, alertColor])

    const handleSetMessageAfterSubmit = (msg, color = "bg-danger") => {
        setMessage(msg)
        setAlertColor(color)
    }

    const handleSubmit = async (event) => {
        event.preventDefault()
        setIsSubmitting(true)
        setMessage("")

        const formData = new FormData(event.target)
        const username = formData.get("username")
        const password = formData.get("password")
        const passwordConfirm = formData.get("passwordConfirm")

        let isValid = true

        if (!emailRegex.test(username)) {
            setValidationFeedback(prev => ({
                ...prev,
                username: { status: 'invalid', message: 'Please enter a valid email address.' }
            }))
            isValid = false
        } else {
            setValidationFeedback(prev => ({
                ...prev,
                username: { status: 'valid', message: 'OK!' }
            }))
        }

        if (password.trim() === "") {
            setValidationFeedback(prev => ({
                ...prev,
                password: { status: 'invalid', message: 'Please enter a valid password.' }
            }))
            isValid = false
        } else {
            setValidationFeedback(prev => ({
                ...prev,
                password: { status: 'valid', message: 'OK!' }
            }))
        }

        if (passwordConfirm.trim() === "" || password !== passwordConfirm) {
            setValidationFeedback(prev => ({
                ...prev,
                passwordConfirm: { status: 'invalid', message: 'Verify your password.' }
            }))
            isValid = false
        } else {
            setValidationFeedback(prev => ({
                ...prev,
                passwordConfirm: { status: 'valid', message: 'OK!' }
            }))
        }

        if (isValid) {
            try {
                const response = await fetch(`${APILocation}/users/register`, {
                    method: 'POST',
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({ username, password })
                })

                const jsonResponse = await response.json()
                if (response.status !== 200) {
                    handleSetMessageAfterSubmit(jsonResponse.message)
                } else {
                    window.location.href = loginRoute
                }
            } catch (error) {
                handleSetMessageAfterSubmit(error.message)
            }
        }

        setIsSubmitting(false)
    }

    return (
        <div className="container mt-5">
            <div className="row">
                <div className="col-xl-6 mx-auto">
                    <div className="card">
                        <div className="card-body">
                            <div className="border p-3 rounded">
                                <h6 className="mb-0 text-uppercase">Register form</h6>
                                <hr />
                                <form className="row g-3" onSubmit={handleSubmit}>
                                    <div className="col-12">
                                        <ValidationInput
                                            label={"Username"}
                                            id={"register-email"}
                                            name={"username"}
                                            type={"email"}
                                            placeholder={"Enter an email"}
                                            idFeedback={validationFeedback.username.status + "ValidationRegisterFeedbackUsername"}
                                            validationStatus={validationFeedback.username.status}
                                            feedBack={validationFeedback.username.message}
                                        />
                                    </div>
                                    <div className="col-12">
                                        <ValidationInput
                                            label={"Password"}
                                            id={"register-password"}
                                            name={"password"}
                                            type={"password"}
                                            placeholder={"Enter a password"}
                                            idFeedback={validationFeedback.password.status + "ValidationRegisterFeedbackPassword"}
                                            validationStatus={validationFeedback.password.status}
                                            feedBack={validationFeedback.password.message}
                                        />
                                    </div>
                                    <div className="col-12">
                                        <ValidationInput
                                            label={"Confirm Password"}
                                            id={"register-password-confirm"}
                                            name={"passwordConfirm"}
                                            type={"password"}
                                            placeholder={"Confirm your password"}
                                            idFeedback={validationFeedback.passwordConfirm.status + "ValidationRegisterFeedbackPasswordConfirm"}
                                            validationStatus={validationFeedback.passwordConfirm.status}
                                            feedBack={validationFeedback.passwordConfirm.message}
                                        />
                                    </div>
                                    <div className="col-12">
                                        <div className="text-white p-2 ms-1 mb-3 d-none" ref={alertRef}>{message}</div>
                                        <div className="d-grid">
                                            <button type="submit" className="btn btn-primary" disabled={isSubmitting}>
                                                {isSubmitting ? 'Signing up...' : 'Sign up'}
                                            </button>
                                        </div>
                                    </div>
                                    <div className="col-12 text-center">
                                        <p className="mb-0">Already have an account? <Link to={loginRoute}>Login</Link></p>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
