import React, { useState, useRef, useContext, useEffect } from 'react'
import { ValidationInput } from '../ValidationInput'
import { TokenContext } from '../../App'

export function TodoForm({ apiEndPoint, method, action, fetchTodos }) {
    const token = useContext(TokenContext)

    const [validationFeedbackForTitle, setValidationFeedbackForTitle] = useState(["", ""])
    const [validationFeedbackForAssignTo, setValidationFeedbackForAssignTo] = useState(["", ""])

    const alertRef = useRef(null)
    const [message, setMessage] = useState("")
    const [alertColor, setAlertColor] = useState("bg-success")

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/

    useEffect(() => {
        if (alertRef.current) {
            if (message) {
                alertRef.current.classList.remove("d-none")
            } else {
                alertRef.current.classList.add("d-none")
            }
        }
    }, [message])

    const handleSetMessageAfterSubmit = (message, color = "bg-danger") => {
        setMessage(message)

        // Set the alert color
        if (color !== alertColor) {
            alertRef.current.classList.remove(alertColor)
            setAlertColor(color)
            alertRef.current.classList.add(color)
        }
    }

    const handleSubmitTodoCRUDForm = (event) => {
        event.preventDefault()

        const datas = new FormData(event.target)
        const title = datas.get("title")
        const assignTo = datas.get("assign_to")

        const isTitleValid = title.length >= 3
        const isAssignToValid = emailRegex.test(assignTo) || assignTo.trim() === ""

        setValidationFeedbackForTitle(isTitleValid ? ["valid", "OK !"] : ["invalid", "Too short."])
        setValidationFeedbackForAssignTo(isAssignToValid ? ["valid", "OK !"] : ["invalid", "Please enter an email."])

        if (isTitleValid && isAssignToValid) {
            const body = Object.fromEntries(datas.entries())

            fetch(apiEndPoint, {
                method: method,
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${token}`,
                },
                body: JSON.stringify(body),
            })
                .then(response => {
                    if (response.status !== 200) {
                        return response.json().then(jsonResponse => {
                            handleSetMessageAfterSubmit(jsonResponse.message)
                        })
                    } else {
                        return response.json().then(jsonResponse => {
                            fetchTodos()
                            handleSetMessageAfterSubmit(jsonResponse.message, "bg-success")
                        })
                    }
                })
                .catch(error => {
                    handleSetMessageAfterSubmit(error.message)
                })
        }
    }

    return (
        <form method="post" className="row g-3" onSubmit={handleSubmitTodoCRUDForm}>
            <div className="col-12">
                <ValidationInput
                    label={"Title"}
                    id={`${action}-title`}
                    name={"title"}
                    type={"text"}
                    placeholder={"Enter a title"}
                    idFeedback={`${validationFeedbackForTitle[0]}ValidationRegisterFeedbackTitle`}
                    validationStatus={validationFeedbackForTitle[0]}
                    feedBack={validationFeedbackForTitle[1]}
                />
            </div>
            <div className="col-12">
                <ValidationInput
                    label={"Assign To"}
                    id={`${action}-assign_to`}
                    name={"assign_to"}
                    type={"email"}
                    placeholder={"Enter an email address"}
                    idFeedback={`${validationFeedbackForAssignTo[0]}ValidationRegisterFeedbackAssignTo`}
                    validationStatus={validationFeedbackForAssignTo[0]}
                    feedBack={validationFeedbackForAssignTo[1]}
                />
            </div>
            <div className="col-12">
                <div className="h5">Priority Level</div>
                <div className="form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="priority" id="high" value="high" />
                    <label className="form-check-label" htmlFor="high">High</label>
                </div>
                <div className="form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="priority" id="medium" value="medium" defaultChecked />
                    <label className="form-check-label" htmlFor="medium">Medium</label>
                </div>
                <div className="form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="priority" id="low" value="low" />
                    <label className="form-check-label" htmlFor="low">Low</label>
                </div>
            </div>
            <div className="col-12">
                <div className="h5">Completion Status</div>
                <div className="form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="status" id="toDo" value="toDo" defaultChecked />
                    <label className="form-check-label" htmlFor="toDo">
                        <small className="bx bxs-circle me-1 priority-toDo"></small>To Do
                    </label>
                </div>
                <div className="form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="status" id="inProgress" value="inProgress" />
                    <label className="form-check-label" htmlFor="inProgress">
                        <small className="bx bxs-circle me-1 priority-inProgress"></small>In Progress
                    </label>
                </div>
                <div className="form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="status" id="done" value="done" />
                    <label className="form-check-label" htmlFor="done">
                        <small className="bx bxs-circle me-1 priority-done"></small>Done
                    </label>
                </div>
            </div>
            <div className="col-12">
                <div className={`text-white p-2 ms-1 mb-3 ${message ? '' : 'd-none'} ${alertColor}`} ref={alertRef}>{message}</div>
                <hr />
                <div className="d-grid gap-2 d-md-block text-end">
                    <button type="button" className="btn btn-secondary me-2" data-bs-dismiss="modal">Close</button>
                    <button type="submit" className="btn btn-primary">Submit</button>
                </div>
            </div>
        </form>
    )
}
