import React, { useContext } from 'react'
import { APILocation, TokenContext } from '../../App'

export function DeleteButton({ todoId, method, fetchTodos }) {
    const token = useContext(TokenContext)

    const handleSubmitDelete = (e) => {
        e.preventDefault()

        fetch(`${APILocation}/todos/${todoId}`, {
            method: method,
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${token}`,
            }
        })
            .then(response => {
                if (response.status === 200) {
                    fetchTodos()
                }
            })
            .catch(error => {
                console.log('An error occured:' + error.message)
            })
    }
    return <form id={'delete_todo_' + todoId} className='d-inline' method="post" onSubmit={handleSubmitDelete}>
        <button className="btn btn-danger btn-sm" type="submit">Supprimer</button>
    </form>
}
