import React from 'react'
import { ModalForm } from './ModalForm'
import { TodoForm } from "./forms/TodoForm"
import { APILocation } from '../App'

export function Main({ children, id, fetchTodos }) {
    const modalId = "createTodo"

    return <div className="container mt-5">
        <div className="row">
            <div className="col col-lg-9 mx-auto">
                <div className="card radius-10">
                    <div className="card-body">
                        <div className="d-flex justify-content-between">
                            <h5 className="card-title">Todo List </h5>
                            <button className="btn btn-primary" type="button"
                                data-bs-toggle="modal" data-bs-target={`#${modalId}`}>Ajouter</button>
                            <ModalForm id={modalId} title={"Create a new Todo"}><TodoForm method={"POST"} action={"create"}
                                apiEndPoint={APILocation + "/todos"} fetchTodos={fetchTodos} /></ModalForm>
                        </div>
                        <div className="my-3 border-top"></div>
                        <div className="accordion accordion-flush" id={id}></div>
                        {children}
                    </div>
                </div>
            </div>
        </div>
    </div>
}
