import React from 'react'

export function ValidationInput({ label, id, name, type, placeholder, validationStatus, idFeedback, feedBack }) {

  if (validationStatus && idFeedback && feedBack) {
    const classForFeedBack = validationStatus + "-feedback"
    const classForAriaDescribedBy = "form-control is-" + validationStatus


    return <>
      <label htmlFor={id} className="form-label">{label}</label>
      <input type={type} name={name} id={id} className={classForAriaDescribedBy}
        placeholder={placeholder} aria-describedby={idFeedback} />
      <div id={idFeedback} className={classForFeedBack}>{feedBack}</div>
    </>
  } else {
    return <>
      <label htmlFor={id} className="form-label">{label}</label>
      <input type={type} name={name} id={id} className={"form-control"}
        placeholder={placeholder} />
    </>
  }
}
