import React, { useState, useRef, useEffect } from 'react'
import { ValidationInput } from "../components/ValidationInput"
import { APILocation, homeRoute, registerRoute } from "../App"
import { Link, useNavigate } from 'react-router-dom'

export function Login({ handleLogin }) {
    const [isSubmitting, setIsSubmitting] = useState(false)
    const [validationFeedback, setValidationFeedback] = useState({
        username: { status: '', message: '' },
        password: { status: '', message: '' }
    })
    const [message, setMessage] = useState("")

    const alertRef = useRef(null)
    const navigate = useNavigate()

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/

    useEffect(() => {
        if (message) {
            alertRef.current.classList.remove("d-none")
        } else {
            alertRef.current.classList.add("d-none")
        }
    }, [message])

    const handleSetMessageAfterSubmit = (msg) => {
        setMessage(msg)
    }

    const handleSubmit = async (event) => {
        event.preventDefault()
        setIsSubmitting(true)
        setMessage("")

        const datas = new FormData(event.target)
        const username = datas.get("username")
        const password = datas.get("password")

        let isValid = true

        if (!emailRegex.test(username)) {
            setValidationFeedback(prev => ({
                ...prev,
                username: { status: 'invalid', message: 'Please enter a valid email address.' }
            }))
            isValid = false
        } else {
            setValidationFeedback(prev => ({
                ...prev,
                username: { status: 'valid', message: 'OK!' }
            }))
        }

        if (password.trim() === "") {
            setValidationFeedback(prev => ({
                ...prev,
                password: { status: 'invalid', message: 'Please enter a valid password.' }
            }))
            isValid = false
        } else {
            setValidationFeedback(prev => ({
                ...prev,
                password: { status: 'valid', message: '' }
            }))
        }

        if (isValid) {
            try {
                const response = await fetch(`${APILocation}/users/login`, {
                    method: 'POST',
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({ username, password })
                })

                const jsonResponse = await response.json()
                if (response.status !== 200) {
                    handleSetMessageAfterSubmit(jsonResponse.message)
                } else {
                    handleLogin(jsonResponse.token)
                    navigate(homeRoute)
                }
            } catch (error) {
                handleSetMessageAfterSubmit(error.message)
            }
        }

        setIsSubmitting(false)
    }

    return (
        <div className="container mt-5">
            <div className="row">
                <div className="col-xl-6 mx-auto">
                    <div className="card">
                        <div className="card-body">
                            <div className="border p-3 rounded">
                                <h6 className="mb-0 text-uppercase">Login form</h6>
                                <hr />
                                <form className="row g-3" onSubmit={handleSubmit}>
                                    <div className="col-12">
                                        <ValidationInput
                                            label={"Username"}
                                            id={"login-email"}
                                            name={"username"}
                                            type={"email"}
                                            placeholder={"Enter an email"}
                                            idFeedback={validationFeedback.username.status + "ValidationLoginFeedbackUsername"}
                                            validationStatus={validationFeedback.username.status}
                                            feedBack={validationFeedback.username.message}
                                        />
                                    </div>
                                    <div className="col-12">
                                        <ValidationInput
                                            label={"Password"}
                                            id={"login-password"}
                                            name={"password"}
                                            type={"password"}
                                            placeholder={"Enter a password"}
                                        />
                                    </div>
                                    <div className="col-12">
                                        <div className="text-white bg-danger p-2 ms-1 mb-3 d-none" ref={alertRef}>{message}</div>
                                        <div className="d-grid">
                                            <button type="submit" className="btn btn-primary" disabled={isSubmitting}>
                                                {isSubmitting ? 'Logging in...' : 'Log in'}
                                            </button>
                                        </div>
                                    </div>
                                    <div className="col-12 text-center">
                                        <p className="mb-0">Not a member? <Link to={registerRoute}>Register</Link></p>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
